.App {
  text-align: center;
  background-color: #E2E2E2;
  min-height: 100vh;
  position: relative;
}

.App-title {
  color: #333333;
}

.App-header {
  height: 50px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 30px;
  align-items: center;
}

.App-footer {
  position: absolute;
  bottom: 0px;
  height: auto;
  width: 100%;
}

.App-footer-container {
  padding: 30px;
  display: flex;
  flex-direction: row;
  align-items: center;
}

h1, h2, h3, h4, p {
  margin: 0;
  padding: 0;
  color: #333333
}
